import { useEffect, useState } from "react";
import { useRouter } from "next/router";

// ประกาศ type สำหรับข้อมูลใน participation และ users
interface Participation {
  activity_id: number;
  student_id: number;
}

interface User {
  id: number;
  name: string;
}

const ActivityParticipantsPage = () => {
  const [participants, setParticipants] = useState<string[]>([]);
  const router = useRouter();
  const { activityId } = router.query;

  useEffect(() => {
    if (!activityId) return;

    // ดึงข้อมูลผู้เข้าร่วมกิจกรรมจาก mock data
    const fetchParticipants = async () => {
      const resParticipation = await fetch("/participation.json");
      const participationData: Participation[] = await resParticipation.json(); // กำหนด type ของ participationData

      const resUsers = await fetch("/users.json");
      const usersData: User[] = await resUsers.json(); // กำหนด type ของ usersData

      const participantsForActivity = participationData
        .filter((p) => p.activity_id === parseInt(activityId as string)) // ใช้ type ของ p ที่ประกาศไว้
        .map((p) => {
          const user = usersData.find((u) => u.id === p.student_id);
          return user ? user.name : "ไม่พบข้อมูล";
        });

      setParticipants(participantsForActivity);
    };

    fetchParticipants();
  }, [activityId]);

  return (
    <div className="min-h-screen px-6 py-10 sm:px-16 bg-gray-50 dark:bg-[#111] text-gray-900 dark:text-white">
      <h2 className="text-2xl font-bold mb-4">รายชื่อผู้เข้าร่วมกิจกรรม</h2>
      <ul>
        {participants.length > 0 ? (
          participants.map((participant, index) => (
            <li key={index}>{participant}</li>
          ))
        ) : (
          <li>ยังไม่มีผู้เข้าร่วมกิจกรรมนี้</li>
        )}
      </ul>
    </div>
  );
};

export default ActivityParticipantsPage;
